import ax from './axios';
import { Observable } from 'rxjs';
class Rx4axios {
    options;
    _httpClient;
    constructor(options = {}) {
        this.options = options;
        this._httpClient = ax;
    }
    _makeRequest(method, url, queryParams, body, headers) {
        let request;
        switch (method) {
            case 'GET':
                request = this._httpClient.get(url, { params: queryParams, headers });
                break;
            case 'POST':
                request = this._httpClient.post(url, body, { params: queryParams, headers });
                break;
            case 'PUT':
                request = this._httpClient.put(url, body, { params: queryParams, headers });
                break;
            case 'PATCH':
                request = this._httpClient.patch(url, body, { params: queryParams, headers });
                break;
            case 'DELETE':
                request = this._httpClient.delete(url, { params: queryParams, headers });
                break;
            default:
                throw new Error('Method not supported');
        }
        return new Observable(subscriber => {
            request.then(response => {
                subscriber.next(response.data);
                subscriber.complete();
            }).catch((err) => {
                subscriber.error(err);
                subscriber.complete();
            });
        });
    }
    get(url, queryParams, headers) {
        return this._makeRequest('GET', url, queryParams, {}, headers);
    }
    post(url, body, queryParams, headers) {
        return this._makeRequest('POST', url, queryParams, body, headers);
    }
    put(url, body, queryParams, headers) {
        return this._makeRequest('PUT', url, queryParams, body, headers);
    }
    patch(url, body, queryParams, headers) {
        return this._makeRequest('PATCH', url, queryParams, body, headers);
    }
    delete(url, queryParams, headers) {
        return this._makeRequest('DELETE', url, queryParams, headers);
    }
}
let Rx4ax = new Rx4axios();
export default Rx4ax;
