import { Vue } from 'vue-class-component';
import { useRouter } from 'vue-router';
import { getCurrentInstance } from 'vue';
export default class BaseVue extends Vue {
    useCurrentInstance() {
        const { appContext } = getCurrentInstance();
        const globalProperties = appContext.config.globalProperties;
        return globalProperties;
    }
    gb_proxy = this.useCurrentInstance();
    gb_router = useRouter();
    $success(msg) {
        this.gb_proxy.$message.success(msg);
    }
    $errors(msg) {
        this.gb_proxy.$message.error(msg);
    }
    $warnings(msg) {
        this.gb_proxy.$message.warning(msg);
    }
    $normals(msg) {
        this.gb_proxy.$message.normal(msg);
    }
    $self(msg) {
        this.gb_proxy.$message.self(msg);
    }
    $reconfirm(options) {
        return this.gb_proxy.$reconfirm(options);
    }
    onSignInOrUp(routerName) {
        if (routerName == 'Sign up')
            this.gb_router.push({ name: 'SignUp' });
        if (routerName == 'Sign in')
            this.setRouter({ name: 'SignIn' });
    }
    setRouter(params) {
        this.gb_router.push(params);
    }
    getRouter() {
        return {
            query: this.gb_router.currentRoute
        };
    }
}
