import { onUnmounted } from 'vue';
import mitt from 'mitt';
const emitter = mitt();
const customEmit = (eventName, arg) => {
    emitter.emit(eventName, arg);
};
const customOn = (eventName, callback) => {
    emitter.on(eventName, (arg) => callback(arg));
};
export const useEventbus = () => {
    onUnmounted(() => {
        emitter.all.clear();
    });
    return {
        customEmit,
        customOn
    };
};
